<template>
  <footer ref="footer">
    <div class="video">
      <video
        v-show="play"
        :key="play"
        ref="video"
        class="w-screen hidden xl:block"
        loop muted>
        <source src="http://api.badranayalaw.credeva.id/footer.mp4" type="video/mp4">
        Sorry, your browser doesn't support embedded videos.
      </video>
    </div>
    <!--  -->
    <div class="container">
      <div class="flex content-between flex-wrap h-screen py-10">
        <div class="w-full">
          <ul class="mb-3">
            <li 
              v-for="item in menu" :key="item.text"
              @click="$router.push({ name: item.route })">
              {{ item.text }}
            </li>
          </ul>
          <div class="flex">
            <a href="" target="_blank" class="icon-circle mr-4">
              <img src="@/assets/icon/fb.svg" alt="fb">
            </a>
            <a href="" target="_blank" class="icon-circle">
              <img src="@/assets/icon/in.svg" alt="fb">
            </a>
          </div>
        </div>
        <div class="footer">
          <div>
