          </div>
          <div>
            &copy; 2020 Badranaya Law Partnership
          </div>
          <div class="hidden md:block">
            <img src="@/assets/img/logo.svg" alt="logo text" width="50" class="mx-auto mb-4">
            <img src="@/assets/img/logo_text.svg" alt="logo text" width="200">
          </div>
        </div>
      </div>
    </div>
  </footer>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(['hidden', 'navigation'])
  },
  data: () => ({
    menu: [
      { text: 'about us', route: 'about' },
      // { text: 'our people', route: '' },
      { text: 'our expertise', route: 'expertise' },
      // { text: 'news', route: '' },
      { text: 'careers', route: 'careers' },
      { text: 'contact us', route: 'contact' },
      { text: 'directory', route: 'directory' }
    ],
    play: false
  }),
  watch: {
    hidden() {
      this.play = true
    }
  },
  methods: {
    handleScroll() {
      const footer = this.$refs.footer.offsetHeight
      const body = document.body.scrollHeight
      const scroll = body - footer - 82 // 82 tinggi navbar
      const scrollNav = body - footer - screen.height// tinggi device
      
      const video = this.$refs.video
      if (window.scrollY >= scroll) {
        this.$store.dispatch('updateHidden', true)        
        video.setAttribute('autoplay', '')
      } else {
        this.$store.dispatch('updateHidden', false)        
      }

      if (window.scrollY >= scrollNav) {
        this.$store.dispatch('updateNavigation', true)
      } else {
        this.$store.dispatch('updateNavigation', false)
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll);
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll);
  }
}
</script>

