<template>
  <header 
    :class="{
      'bg-primary': !transparent,
      'hidden': hidden,
    }">
    <div class="container">
      <div class="navbar">
        <div class="cursor-pointer">
          <img class="logo" @click="$router.push({name: 'home'})" src="@/assets/img/logo.svg" alt="logo">
        </div>
        <nav class="hidden xl:flex">
          <div 
            v-for="item in nav" :key="item.text"
            @click="$router.push({name: item.route})">
            {{ item.text }}
          </div>
        </nav>
        <div class="flex items-center relative">
          <!-- <img class="icon-search mr-8" src="@/assets/icon/search.svg" alt="icon"> -->
          <div 
            class="burger-menu" 
            :class="{'open': active}"
            @click="$store.dispatch('updateActive', true)">
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(['hidden', 'active'])
  },
  data: () => ({
    nav: [
      { text: 'about us', route: 'about' },
      { text: 'our expertise', route: 'expertise' },
      { text: 'contact', route: 'contact' }
    ],
    transparent: true
  }),
  methods: {
    handleScroll() {
      if (window.scrollY > 400) {
        this.transparent = false
      } else {
        this.transparent = true        
      }
    }
  },  
  created () {
    window.addEventListener('scroll', this.handleScroll);
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll);
  }
}
</script>

<style scoped>
header{
  @apply fixed w-full z-10 duration-100 top-0;
}
.navbar{
  @apply flex justify-between items-center py-4 text-white font-sans;
}
nav{
  @apply flex;
}
nav div{
  @apply duration-200 cursor-pointer uppercase px-3;
}
nav div:hover{
  @apply opacity-75;
}
.logo{
  width: 40px;
}
.icon-search{
  width: 25px;
}
.burger-menu{
  width: 30px;
  height: 20px;
  @apply flex content-between flex-wrap cursor-pointer overflow-hidden;
}
.burger-menu span{
  width: 100%;
  height: 2px;
  @apply bg-white;
}
.burger-menu.open span:nth-child(1) {
  transform: translateX(100%);
  transition: .25s ease-in-out;
}
.burger-menu.open span:nth-child(2) {
  transform: translateX(100%);
  transition: .5s ease-in-out;
}
.burger-menu.open span:nth-child(3) {
  transform: translateX(100%);
  transition: .75s ease-in-out;
}
@screen md{
  .logo{
    width: 50px;
  }
  .icon-search{
    width: 30px;
  }
  .burger-menu{
    width: 40px;
    height: 25px;
  }
}
</style>
